* {
  box-sizing: border-box;
}

body {
  font-family: Montserrat, sans-serif;
}

::-webkit-scrollbar {
  width: 15px;
}

::-webkit-scrollbar-track {
  background-color: #54043d;
}

::-webkit-scrollbar-thumb {
  background-color: rgba(0, 0, 0, .56);
  border: 3px solid #54043d;
  border-radius: 10px;
}

::-webkit-scrollbar-thumb:hover {
  background-color: #000;
}

input[type="button"] {
  height: 40px;
  width: 120px;
  cursor: pointer;
  color: #fff;
  background-color: #2a2052;
  border: 2px solid #f03;
  border-radius: 10px;
  outline: none;
  justify-content: center;
  align-items: center;
  font-size: 15px;
  transition: all 1s cubic-bezier(.4, 0, .2, 1);
  display: flex;
}

.button {
  height: 40px;
  width: 120px;
  cursor: pointer;
  color: #fff;
  background-color: #2a2052;
  border: 2px solid #f03;
  border-radius: 10px;
  outline: none;
  font-size: 15px;
}

.todo-container {
  width: 600px;
}

.menu {
  fill: #f03;
}

.showBtn {
  cursor: pointer;
  width: 40px;
  height: 40px;
  background-color: #2a2052;
  border: 2px solid #f03;
  border-radius: 10px;
  outline: none;
  justify-content: center;
  align-items: center;
  display: flex;
}

@media screen and (max-width: 500px) {
  .showBtn {
    font-size: 30px;
  }
}

.newtask-box {
  flex-direction: column;
  align-items: center;
  margin-bottom: 25px;
  display: flex;
}

.newtask-box h1 {
  color: #fff;
  justify-content: center;
  font-size: 30px;
  display: flex;
}

.textarea__wrapper {
  position: relative;
}

#newTask {
  min-width: 500px;
  max-width: 800px;
  min-height: 50px;
  max-height: 200px;
  border: 2px solid #f03;
  border-radius: 10px;
  outline: none;
  padding-top: 10px;
  padding-left: 20px;
}

#newTask:hover {
  min-height: 100px;
  max-height: 200px;
}

@media screen and (max-width: 530px) {
  #newTask {
    min-width: 300px;
    max-width: 300px;
  }
}

#char_count {
  color: #000;
  position: absolute;
  bottom: 10px;
  right: 10px;
}

.todo-list {
  width: 80%;
  flex-direction: column;
  gap: 20px;
  padding-bottom: 50px;
  padding-right: 20px;
  display: flex;
  overflow-y: auto;
}

.todo-list .task-item {
  max-height: 60px;
  justify-content: space-between;
  align-items: center;
  gap: 20px;
  display: flex;
}

.todo-list .task-item p {
  color: #fff;
  font-size: 12px;
}

@media screen and (min-width: 500px) {
  .todo-list .task-item p {
    font-size: 30px;
  }
}

.todo-list .task-item__wrapper {
  gap: 10px;
  display: flex;
}

.todo-list .task-item__wrapper:hover input[type="button"] {
  display: block;
}

.todo-list .task-item__wrapper input[type="button"] {
  display: none;
}

@media screen and (max-width: 500px) {
  .todo-list .task-item__wrapper input[type="button"] {
    height: 30px;
    width: 70px;
    font-size: 10px;
  }
}

.todo-list .task-item .done {
  color: #766cff;
  text-decoration: line-through;
}

#clearList {
  display: none;
  position: absolute;
  bottom: 50px;
}

.box {
  --border-size: 10px;
  --border-angle: 0turn;
  width: 100%;
  height: 100%;
  background-image: conic-gradient(from var(--border-angle), #213, #112 50%, #213), conic-gradient(from var(--border-angle), transparent 20%, #08f, #f03);
  background-size: calc(100% - var(--border-size) * 2) calc(100% - var(--border-size) * 2), cover;
  background-position: center;
  background-repeat: no-repeat;
  flex-direction: column;
  align-items: center;
  padding-top: 50px;
  padding-bottom: 100px;
  animation: bg-spin 3s linear infinite;
  display: flex;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.box h2 {
  color: #fff;
}

@keyframes bg-spin {
  to {
    --border-angle: 1turn;
  }
}

@property --border-angle {
  syntax: "<angle>";
  inherits: true;
  initial-value: 0turn;
}

